.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-img {
  display: block;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item {
  list-style: none;
}

.header__item:not(:last-child) {
  margin-right: 20px;
}

.header__link {
  text-transform: uppercase;
  color: inherit;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.header__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 100%;
  max-width: 200px;
  padding: 32px 16px 16px;
  font-size: 12px;
  line-height: 18px;
  transition: transform .3s;
}

.card__title, .card__info-text {
  margin: 0;
}

.card__img {
  width: 100%;
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  font: inherit;
  margin-bottom: 4px;
  font-weight: 500;
  transition: color .3s;
}

.card__info-text {
  margin-bottom: 16px;
}

.card__reviews-wrapper {
  margin-bottom: 24px;
}

.card__reviews-wrapper, .card__price-wrapper {
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  display: flex;
}

.card__reviews-text, .card__info-text {
  font-size: 10px;
  line-height: 14px;
}

.card__price-wrapper {
  margin-bottom: 16px;
}

.card__info-text, .card__price-text {
  color: #616070;
}

.card__price {
  font-size: 16px;
  font-weight: 700;
}

.card__link {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #0000;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

